'use client'

import { useRef, useState } from 'react'
import { LexicalComposer } from '@lexical/react/LexicalComposer'
import { RichTextPlugin } from '@lexical/react/LexicalRichTextPlugin'
import { ContentEditable } from '@lexical/react/LexicalContentEditable'
import { HistoryPlugin } from '@lexical/react/LexicalHistoryPlugin'
import { OnChangePlugin } from '@lexical/react/LexicalOnChangePlugin'
import { LexicalErrorBoundary } from '@lexical/react/LexicalErrorBoundary'
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext'
import {
  EditorState,
  FORMAT_TEXT_COMMAND,
  REDO_COMMAND,
  UNDO_COMMAND,
  $getSelection,
  $isRangeSelection,
  $createParagraphNode,
} from 'lexical'
import { $setBlocksType } from '@lexical/selection'
import { HeadingNode, QuoteNode, $createHeadingNode, $createQuoteNode } from '@lexical/rich-text'
import {
  ListNode,
  ListItemNode,
  INSERT_UNORDERED_LIST_COMMAND,
  INSERT_ORDERED_LIST_COMMAND,
  REMOVE_LIST_COMMAND,
} from '@lexical/list'
import { LinkNode, TOGGLE_LINK_COMMAND } from '@lexical/link'
import { Button } from '@/components/ui/button'

interface LexicalEditorProps {
  initialContent?: string
  onChange: (content: string) => void
  onImageUpload?: (url: string) => void
}

function Toolbar() {
  const [editor] = useLexicalComposerContext()

  const setHeading = (level: 'paragraph' | 'h1' | 'h2' | 'h3' | 'quote') => {
    editor.update(() => {
      const selection = $getSelection()
      if ($isRangeSelection(selection)) {
        if (level === 'paragraph') {
          $setBlocksType(selection, () => $createParagraphNode())
        } else if (level === 'quote') {
          $setBlocksType(selection, () => $createQuoteNode())
        } else {
          $setBlocksType(selection, () => $createHeadingNode(level))
        }
      }
    })
  }

  const promptLink = () => {
    const url = window.prompt('Enter URL (leave empty to remove)')
    if (url) {
      editor.dispatchCommand(TOGGLE_LINK_COMMAND, url)
    } else {
      editor.dispatchCommand(TOGGLE_LINK_COMMAND, null)
    }
  }

  return (
    <div className="flex flex-wrap items-center gap-2 p-2 border-b bg-gray-50">
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(UNDO_COMMAND, undefined)}>Undo</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(REDO_COMMAND, undefined)}>Redo</Button>
      <div className="w-px h-5 bg-gray-300 mx-1" />
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'bold')}>B</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'italic')}>I</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'underline')}>U</Button>
      <div className="w-px h-5 bg-gray-300 mx-1" />
      <Button type="button" variant="outline" size="sm" onClick={() => setHeading('paragraph')}>P</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => setHeading('h1')}>H1</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => setHeading('h2')}>H2</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => setHeading('h3')}>H3</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => setHeading('quote')}>Quote</Button>
      <div className="w-px h-5 bg-gray-300 mx-1" />
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(INSERT_UNORDERED_LIST_COMMAND, undefined)}>â€¢ List</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(INSERT_ORDERED_LIST_COMMAND, undefined)}>1. List</Button>
      <Button type="button" variant="outline" size="sm" onClick={() => editor.dispatchCommand(REMOVE_LIST_COMMAND, undefined)}>No list</Button>
      <div className="w-px h-5 bg-gray-300 mx-1" />
      <Button type="button" variant="outline" size="sm" onClick={promptLink}>Link</Button>
    </div>
  )
}

export default function LexicalEditor({ initialContent, onChange }: LexicalEditorProps) {
  const initialConfig = {
    namespace: 'BlogEditor',
    theme: {
      paragraph: 'mb-2',
      text: {
        bold: 'font-bold',
        italic: 'italic',
        underline: 'underline',
      },
    },
    nodes: [HeadingNode, QuoteNode, ListNode, ListItemNode, LinkNode],
    onError: (error: Error) => {
      console.error(error)
    },
    editorState: initialContent,
  }

  const handleEditorChange = (editorState: EditorState) => {
    const json = JSON.stringify(editorState.toJSON())
    onChange(json)
  }

  return (
    <div className="border rounded-lg overflow-hidden">
      <LexicalComposer initialConfig={initialConfig}>
        <Toolbar />
        <div className="relative">
          <RichTextPlugin
            contentEditable={<ContentEditable className="min-h-[400px] p-4 outline-none prose max-w-none" />}
            placeholder={<div className="absolute top-4 left-4 text-gray-400 pointer-events-none">Start writing your blog post...</div>}
            ErrorBoundary={LexicalErrorBoundary}
          />
          <HistoryPlugin />
          <OnChangePlugin onChange={handleEditorChange} />
        </div>
      </LexicalComposer>
    </div>
  )
}
